<template>
  <div ref="echarts"></div>
</template>
<script>
import * as echarts from 'echarts'
export default {
  props: {
    chartData: {
      type: Object,
      default() {
        return {
          xData: [],
          series: []
        }
      }
    },
    isAxisechart: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      AxisOption: {
        legend: {
          // 图例文字颜色
          textStyle: {
            color: '#333'
          }
        },
        grid: {
          top: '25%',
          left: '5%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        // 提示框
        tooltip: {
          trigger: 'axis'
        },
        xAxis: {
          type: 'category', // 类目轴
          data: [],
          axisLine: {
            lineStyle: {
              color: '#17b3a3'
            }
          },
          axisLabel: {
            interval: 0,
            color: '#333'
          }
        },
        yAxis: [
          {
            type: 'value',
            axisLine: {
              lineStyle: {
                color: '#17b3a3'
              }
            }
          }
        ],
        color: [
          '#2ec7c9',
          '#b6a2de',
          '#5ab1ef',
          '#ffb980',
          '#d87a80',
          '#8d98b3'
        ],
        series: []
      },
      normalOption: {
        tooltip: {
          trigger: 'item'
        },
        color: [
          '#0f78f4',
          '#dd536b',
          '#9462e5',
          '#a6a6a6',
          '#e1bb22',
          '#39c362',
          '#3ed1cf'
        ],
        series: []
      },
      echart: null
    }
  },
  computed: {
    Options() {
      return this.isAxisechart ? this.AxisOption : this.normalOption
    }
  },
  methods: {
    initChart() {
      this.initChartData()
      // 设置echarts表格
      if (this.echart) {
        this.echart.setOption(this.Options)
      } else {
        this.echart = echarts.init(this.$refs.echarts)
        this.echart.setOption(this.Options)
      }
    },
    initChartData() {
      if (this.isAxisechart) {
        this.AxisOption.xAxis.data = this.chartData.xData
        this.AxisOption.series = this.chartData.series
      } else {
        this.normalOption.series = this.chartData.series
      }
    }
  },
  watch: {
    chartData: {
      handler: function () {
        this.initChart()
      },
      deep: true
    }
  }
}
</script>
<style></style>
