<template>
  <el-row class="home" :gutter="20">
    <el-col :span="6" style="margin-top: 20px">
      <el-card shadow="hover">
        <div class="user">
          <img :src="userImg" alt="" />
          <div class="userInfo">
            <p class="role"><span>用户名:</span>呆龙</p>
            <p class="roleUser">
              <span>权限:</span
              >&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;超级管理员
            </p>
          </div>
        </div>
        <div class="login-info">
          <p>上次登录时间:<span>2022-1-8</span></p>
          <p>上次登录地点:<span>上海</span></p>
        </div>
      </el-card>
      <el-card style="height: 470px; margin-top: 20px" shadow="hover">
        <el-table :data="tableData">
          <el-table-column
            :prop="key"
            :label="value"
            show-overflow-tooltip
            v-for="(value, key) in tableLabel"
            :key="key"
          >
          </el-table-column>
        </el-table>
      </el-card>
    </el-col>
    <el-col :span="16" style="padding-top: 20px" :offset="1">
      <div class="num">
        <el-card
          shadow="hover"
          :body-style="{ display: 'flex', padding: 0 }"
          v-for="item in countData"
          :key="item.name"
        >
          <i
            class="icon"
            :class="`el-icon-${item.icon}`"
            :style="{ background: item.color }"
          ></i>
          <div class="detail">
            <p class="number">￥{{ item.value }}</p>
            <p class="txt">{{ item.name }}</p>
          </div>
        </el-card>
      </div>
      <el-card shadow="hover">
        <!-- <div ref="echarts" style="height: 280px"></div> -->
        <my-echarts
          :chartData="echartData.order"
          style="height: 260px"
        ></my-echarts>
      </el-card>
      <div class="graph">
        <el-card shadow="hover">
          <!-- <div ref="userEcharts" style="height: 260px"></div> -->
          <my-echarts
            :chartData="echartData.user"
            style="height: 260px"
          ></my-echarts>
        </el-card>
        <el-card shadow="hover">
          <!-- <div ref="videoEcharts" style="height: 260px"></div> -->
          <my-echarts
            :chartData="echartData.video"
            :isAxisechart="false"
            style="height: 260px"
          ></my-echarts>
        </el-card>
      </div>
    </el-col>
  </el-row>
</template>
<script>
// import * as echarts from 'echarts'
import MyEcharts from '../components/Echarts.vue'
export default {
  name: 'Home',
  data() {
    return {
      userImg: require('../assets/image/2.jpg'),
      tableData: [],
      tableLabel: {
        name: '今日课程',
        todayBuy: '今日购买',
        monthBuy: '本月购买',
        totalBuy: '总购买'
      },
      countData: [
        {
          name: '今日支付订单',
          value: 1234,
          icon: 'success',
          color: '#2ec7c9'
        },
        {
          name: '今日收藏订单',
          value: 210,
          icon: 'star-on',
          color: '#ffb980'
        },
        {
          name: '今日未支付订单',
          value: 1234,
          icon: 's-goods',
          color: '#5ab1ef'
        },
        {
          name: '本月支付订单',
          value: 1234,
          icon: 'success',
          color: '#2ec7c9'
        },
        {
          name: '本月收藏订单',
          value: 210,
          icon: 'star-on',
          color: '#ffb980'
        },
        {
          name: '本月未支付订单',
          value: 1234,
          icon: 's-goods',
          color: '#5ab1ef'
        }
      ],
      echartsData: {
        order: {
          legend: {
            // 图例文字颜色
            textStyle: {
              color: '#333'
            }
          },
          grid: {
            left: '20%'
          },
          // 提示框
          tooltip: {
            trigger: 'axis'
          },
          xAxis: {
            type: 'category', // 类目轴
            data: [],
            axisLine: {
              lineStyle: {
                color: '#17b3a3'
              }
            },
            axisLabel: {
              interval: 0,
              color: '#333'
            }
          },
          yAxis: [
            {
              type: 'value',
              axisLine: {
                lineStyle: {
                  color: '#17b3a3'
                }
              }
            }
          ],
          color: [
            '#2ec7c9',
            '#b6a2de',
            '#5ab1ef',
            '#ffb980',
            '#d87a80',
            '#8d98b3'
          ],
          series: []
        },
        user: {
          legend: {
            // 图例文字颜色
            textStyle: {
              color: '#333'
            }
          },
          grid: {
            left: '20%'
          },
          // 提示框
          tooltip: {
            trigger: 'axis'
          },
          xAxis: {
            type: 'category', // 类目轴
            data: [],
            axisLine: {
              lineStyle: {
                color: '#17b3a3'
              }
            },
            axisLabel: {
              interval: 0,
              color: '#333'
            }
          },
          yAxis: [
            {
              type: 'value',
              axisLine: {
                lineStyle: {
                  color: '#17b3a3'
                }
              }
            }
          ],
          color: ['#2ec7c9', '#b6a2de'],
          series: []
        },
        video: {
          tooltip: {
            trigger: 'item'
          },
          color: [
            '#0f78f4',
            '#dd536b',
            '#9462e5',
            '#a6a6a6',
            '#e1bb22',
            '#39c362',
            '#3ed1cf'
          ],
          series: []
        }
      },
      // 要传给组件的值
      echartData: {
        order: {
          xData: [],
          series: []
        },
        user: {
          xData: [],
          series: []
        },
        video: {
          series: []
        }
      }
    }
  },
  methods: {
    getTableData() {
      this.$http.get('http://localhost:3001/tableData').then((res) => {
        this.tableData = res.data.data.tableData
        // console.log(res.data.data)
        // 折线图得展示
        // const order = res.data.orderData
        // this.echartsData.order.xAxis.data = order.date
        // console.log(res)
        // const KeyArray = Object.keys(order.data[0])
        // KeyArray.forEach((key) => {
        //   this.echartsData.order.series.push({
        //     name: key,
        //     type: 'line',
        //     data: order.data.map((item) => item[key])
        //   })
        // })
        // const myEchartsOrder = echarts.init(this.$refs.echarts)
        // myEchartsOrder.setOption(this.echartsData.order)

        // 折线图组件的展示
        const order = res.data.data.orderData
        this.echartData.order.xData = order.date
        const KeyArray = Object.keys(order.data[0])
        KeyArray.forEach((key) => {
          this.echartData.order.series.push({
            name: key,
            type: 'line',
            data: order.data.map((item) => item[key])
          })
        })

        // 柱状图的展示
        // this.echartsData.user.xAxis.data = res.data.userData.map(
        //   (item) => item.date
        // )
        // this.echartsData.user.series.push({
        //   name: '新增用户',
        //   data: res.data.userData.map((item) => item.new),
        //   type: 'bar'
        // })
        // this.echartsData.user.series.push({
        //   name: '活跃用户',
        //   data: res.data.userData.map((item) => item.active),
        //   type: 'bar'
        // })
        // const myEchartsUser = echarts.init(this.$refs.userEcharts)
        // myEchartsUser.setOption(this.echartsData.user)

        // 柱状图组件的展示
        this.echartData.user.xData = res.data.data.userData.map(
          (item) => item.date
        )
        this.echartData.user.series.push({
          name: '新增用户',
          data: res.data.data.userData.map((item) => item.new),
          type: 'bar'
        })
        this.echartData.user.series.push({
          name: '活跃用户',
          data: res.data.data.userData.map((item) => item.active),
          type: 'bar'
        })

        // 饼状图展示
        // this.echartsData.video.series.push({
        //   data: res.data.videoData,
        //   type: 'pie'
        // })
        // const myEchartsVideo = echarts.init(this.$refs.videoEcharts)
        // myEchartsVideo.setOption(this.echartsData.video)

        // 饼状图组件展示
        this.echartData.video.series.push({
          data: res.data.data.videoData,
          type: 'pie'
        })
      })
    }
  },
  components: {
    MyEcharts
  },
  mounted() {
    this.getTableData()
  }
}
</script>
<style scoped lang="scss">
// @import '~@/assets/scss/home';
.home {
  .user {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid #ccc;
    img {
      width: 150px;
      height: 150px;
      border-radius: 50%;
      margin-right: 40px;
    }
    .userInfo {
      .role {
        font-size: 32px;
        margin-bottom: 10px;
      }
      .roleUser {
        color: #999999;
      }
    }
  }
  .login-info {
    p {
      line-height: 28px;
      font-size: 14px;
      color: #999999;
      span {
        color: #666666;
        margin-left: 60px;
      }
    }
  }
  .num {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .el-card {
      width: 32%;
      margin-bottom: 20px;
    }
    .icon {
      font-size: 30px;
      width: 80px;
      height: 80px;
      text-align: center;
      line-height: 80px;
      color: #fff;
    }
    .detail {
      margin-left: 15px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .number {
        font-size: 30px;
        margin-bottom: 10px;
      }
      .txt {
        font-size: 14px;
        text-align: center;
        color: #999999;
      }
    }
  }
  .graph {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
    .el-card {
      width: 48%;
    }
  }
}
</style>
